import { makeAutoObservable } from 'mobx';
import i18n from 'i18next';

// 
class ConfigStore {
  constructor() {
    makeAutoObservable(this);
  }
  activeItem = ''; // 
  locale = localStorage.getItem('locale') ? localStorage.getItem('locale') : 'zh_CN'; // 
  themeStyle = localStorage.getItem('themeStyle') ? localStorage.getItem('themeStyle') : 'dark'; // 
  theme = {
    primaryColor: localStorage.getItem('primaryColor') ? localStorage.getItem('primaryColor') : '#1890FF',
    errorColor: '#ff4d4f',
    warningColor: '#faad14',
    successColor: '#52c41a',
    infoColor: '#1890ff',
  };

  // 
  switchMenuItem = (item) => {
    this.activeItem = item.key;
    localStorage.setItem('activeItem', item.key);
  };

  // 
  crumbItem = () => {
    this.activeItem = '';
    localStorage.removeItem('activeItem');
  };

  // 
  switchLanguage = (lang) => {
    this.locale = lang;
    localStorage.setItem('locale', lang);
    i18n.changeLanguage(lang === 'zh_CN' ? 'zh' : 'en');
  };

  // 
  switchStyle = (style) => {
    this.themeStyle = style;
    localStorage.setItem('themeStyle', style);
  };

  // 
  switchColor = (color) => {
    this.theme.primaryColor = color;
    localStorage.setItem('primaryColor', color);
  };
}

export default ConfigStore;
